.header {
	width: 100%;
	background-color: #FFE44D;
	padding: 12px 0;
}

.header__wrapper {
	display: flex;
	align-items: center;
	justify-content: space-between;
	gap: 80px;
}

@media (max-width: 700px) {
	.header__wrapper {
		flex-direction: column;
		align-items: stretch;
		gap: 20px;
	}
}
.profile{
	display: flex;
	flex-direction: column;
	font-size: 18px;
	font-weight: 600;
	align-items: flex-end;
}

.iconsMenu {
	display: flex;
}

.favoritesLink {
	position: relative;
	width: 34px;
	height: 34px;
}
.iconsMenu svg{
	width: 100%;
	height: 100%;
}

.iconBubble{
	background-color: #01A54E;
	border-radius: 50%;
	color: #FFFFFF;
	font-size: 14px;
	width: 22px;
	height: 22px;
	line-height: 1.25;
	display: flex;
	align-items: center;
	justify-content: center;
	box-sizing: border-box;
	position: absolute;
	bottom: 60%;
	left: 70%;
}

.button {
	margin-left: 20px;
	font: bold 18px Arial;
	text-decoration: none;
	background-color: #FFE44D;
	color: #333333;
	border-radius: 45%;
	padding: 6px 6px 6px 6px;
	border: 2px solid #333333;
	
  }